///////////////////////////
///// State Values /////
///////////////////////////
const initialState = {
	hasLoaded: false,
	isShowing: false,
	voiceLevel: 1,
	voiceActive: false,
	health: 0,
	armor: 0,
	hunger: 0,
	thirst: 0,
	stress: 0,
	voiceColor: 'rgb(255, 89, 0)',
	voiceActiveColor: 'rgb(255, 255, 0)',
	healthColor: 'rgb(255, 0, 0)',
	armorColor: 'rgb(0, 0, 255)',
	hungerColor: 'rgb(0, 255, 0)',
	thirstColor: 'rgb(0, 208, 255)',
	stressColor: 'rgb(170, 0, 255)',
};

///////////////////////////
///// Management /////
///////////////////////////
const Hud = (state = initialState, data) => {
	switch (data.type) {
		case 'setDefaults':
			return {
				...state,
				voiceColor:
					data.values.voiceColor !== undefined
						? data.values.voiceColor
						: state.voiceColor,
				voiceActiveColor:
					data.values.voiceActiveColor !== undefined
						? data.values.voiceActiveColor
						: state.voiceActiveColor,
				healthColor:
					data.values.healthColor !== undefined
						? data.values.healthColor
						: state.healthColor,
				armorColor:
					data.values.armorColor !== undefined
						? data.values.armorColor
						: state.armorColor,
				hungerColor:
					data.values.hungerColor !== undefined
						? data.values.hungerColor
						: state.hungerColor,
				thirstColor:
					data.values.thirstColor !== undefined
						? data.values.thirstColor
						: state.thirstColor,
				stressColor:
					data.values.stressColor !== undefined
						? data.values.stressColor
						: state.stressColor,
				hasLoaded: true,
			};
		case 'init':
			return {
				...state,
				isShowing: true,
				health: data.values.health,
				armor: data.values.armor,
				hunger: data.values.hunger,
				thirst: data.values.thirst,
				stress: data.values.stress,
			};
		case 'update':
			return {
				...state,
				health:
					data.values.health !== undefined ? data.values.health : state.health,
				armor:
					data.values.armor !== undefined ? data.values.armor : state.armor,
				hunger:
					data.values.hunger !== undefined ? data.values.hunger : state.hunger,
				thirst:
					data.values.thirst !== undefined ? data.values.thirst : state.thirst,
				stress:
					data.values.stress !== undefined ? data.values.stress : state.stress,
			};
		case 'toggleVoice':
			return {
				...state,
				voiceActive: data.value,
			};
		case 'voiceLevel':
			return {
				...state,
				voiceLevel: data.value,
			};
		case 'changeColor':
			return {
				...state,
				voiceColor:
					data.values.voiceColor !== undefined
						? data.values.voiceColor
						: state.voiceColor,
				voiceActiveColor:
					data.values.voiceActiveColor !== undefined
						? data.values.voiceActiveColor
						: state.voiceActiveColor,
				healthColor:
					data.values.healthColor !== undefined
						? data.values.healthColor
						: state.healthColor,
				armorColor:
					data.values.armorColor !== undefined
						? data.values.armorColor
						: state.armorColor,
				hungerColor:
					data.values.hungerColor !== undefined
						? data.values.hungerColor
						: state.hungerColor,
				thirstColor:
					data.values.thirstColor !== undefined
						? data.values.thirstColor
						: state.thirstColor,
				stressColor:
					data.values.stressColor !== undefined
						? data.values.stressColor
						: state.stressColor,
			};
		default:
			return state;
	}
};

export default Hud;
