///////////////////////////
///// Imports /////
///////////////////////////
import { Nui } from '../Nui/NuiHandler';
import { store } from '../../index';
import Hud from '../Hud/Main';
import Menu from '../Options/Main';
import './Main.scss';

///////////////////////////
///// NUI /////
///////////////////////////
Nui.registerEmit('init', data => {
	store.dispatch({
		type: 'init',
		values: {
			health: data.health,
			armor: data.armor,
			hunger: data.hunger,
			thirst: data.thirst,
			stress: data.stress,
		},
	});
});

Nui.registerEmit('update', data => {
	store.dispatch({
		type: 'update',
		values: {
			health: data.health,
			armor: data.armor,
			hunger: data.hunger,
			thirst: data.thirst,
			stress: data.stress,
		},
	});
});

Nui.registerEmit('toggleVoice', data => {
	store.dispatch({ type: 'toggleVoice', value: data.value });
});

Nui.registerEmit('voiceLevel', data => {
	store.dispatch({ type: 'voiceLevel', value: data.value });
});

Nui.registerEmit('showMenu', () => {
	store.dispatch({ type: 'showMenu' });
});

///////////////////////////
///// Main /////
///////////////////////////
export default function App() {
	return (
		<section className='mainWindow'>
			<Hud />
			<Menu />
		</section>
	);
}
