///////////////////////////
///// Imports /////
///////////////////////////
import { useSelector } from 'react-redux';
import { store } from '../../index';
import clsx from 'clsx';
import axios from 'axios';
import Config from '../Nui/Config';
import ColourWheel from '../../components/Options/ColourWheel';
import { Radio, Button } from '@material-ui/core';
import { makeStyles, createStyles } from '@material-ui/core/styles';
import SaveIcon from '@material-ui/icons/Save';
import { ReactComponent as ExitIcon } from '../../assets/exit.svg';
import { ReactComponent as VoiceIcon } from '../../assets/voice.svg';
import { ReactComponent as HealthIcon } from '../../assets/health.svg';
import { ReactComponent as ArmorIcon } from '../../assets/armor.svg';
import { ReactComponent as HungerIcon } from '../../assets/hunger.svg';
import { ReactComponent as ThirstIcon } from '../../assets/thirst.svg';
import { ReactComponent as StressIcon } from '../../assets/stress.svg';
import './Main.scss';

///////////////////////////
///// Default /////
///////////////////////////
/* Leave credits pl0xx ^_^ */
const useStyles = makeStyles(() =>
	createStyles({
		button: {
			backgroundColor: 'rgb(0, 0, 172)',
			color: '#fff',
		},
	})
);

export default function Menu() {
	const values = useSelector(state => state.Hud);
	const options = useSelector(state => state.Menu);
	const exitMenu = () => {
		axios
			.post(`http://${Config.resName}/exitmenu`, {})
			.then()
			.catch(err => console.log(err));
		store.dispatch({
			type: 'exit',
		});
	};
	const classes = useStyles();
	const getVoiceColor = () => {
		if (options.activeElement === 'voiceActive') {
			return values.voiceActiveColor;
		}
		return values.voiceColor;
	};
	const colorVoiceDefault = () => {
		if (options.activeElement === 'voice') {
			return true;
		}
		return false;
	};
	const colorVoiceActive = () => {
		if (options.activeElement === 'voiceActive') {
			return true;
		}
		return false;
	};
	const elementActive = elem => {
		if (elem === 'voice') {
			if (
				options.activeElement === 'voice' ||
				options.activeElement === 'voiceActive'
			) {
				return 'green';
			}
		} else {
			if (elem === options.activeElement) {
				return 'green';
			}
		}
		return 'transparent';
	};
	const changeColor = color => {
		if (options.activeElement === 'voice') {
			store.dispatch({
				type: 'changeColor',
				values: { voiceColor: color },
			});
		} else if (options.activeElement === 'voiceActive') {
			store.dispatch({
				type: 'changeColor',
				values: { voiceActiveColor: color },
			});
		} else if (options.activeElement === 'health') {
			store.dispatch({
				type: 'changeColor',
				values: { healthColor: color },
			});
		} else if (options.activeElement === 'armor') {
			store.dispatch({
				type: 'changeColor',
				values: { armorColor: color },
			});
		} else if (options.activeElement === 'hunger') {
			store.dispatch({
				type: 'changeColor',
				values: { hungerColor: color },
			});
		} else if (options.activeElement === 'thirst') {
			store.dispatch({
				type: 'changeColor',
				values: { thirstColor: color },
			});
		} else if (options.activeElement === 'stress') {
			store.dispatch({
				type: 'changeColor',
				values: { stressColor: color },
			});
		}
	};
	const setCurrentElement = selection => {
		store.dispatch({
			type: 'setElement',
			value: selection,
		});
	};
	const getSavedValues = () => {
		if (!values.hasLoaded) {
			let defaults = {};
			if (localStorage.getItem('voiceActive')) {
				values.voiceActiveColor = localStorage.getItem('voiceActive');
			}
			if (localStorage.getItem('voice')) {
				values.voiceColor = localStorage.getItem('voice');
			}
			if (localStorage.getItem('health')) {
				values.healthColor = localStorage.getItem('health');
			}
			if (localStorage.getItem('armor')) {
				values.armorColor = localStorage.getItem('armor');
			}
			if (localStorage.getItem('hunger')) {
				values.hungerColor = localStorage.getItem('hunger');
			}
			if (localStorage.getItem('thirst')) {
				values.thirstColor = localStorage.getItem('thirst');
			}
			if (localStorage.getItem('stress')) {
				values.stressColor = localStorage.getItem('stress');
			}
			store.dispatch({
				type: 'setDefaults',
				values: defaults,
			});
		}
	};
	getSavedValues();
	const saveValues = () => {
		localStorage.setItem('voiceActive', values.voiceActiveColor);
		localStorage.setItem('voice', values.voiceColor);
		localStorage.setItem('health', values.healthColor);
		localStorage.setItem('armor', values.armorColor);
		localStorage.setItem('hunger', values.hungerColor);
		localStorage.setItem('thirst', values.thirstColor);
		localStorage.setItem('stress', values.stressColor);
	};
	return (
		<div
			className={clsx('menu', {
				'menu-Show': options.menuActive,
			})}
		>
			<ExitIcon className='exit' onClick={exitMenu} />
			<Button
				variant='contained'
				classes={{ root: classes.button }}
				className='save'
				onClick={saveValues}
				startIcon={<SaveIcon />}
			>
				Save
			</Button>
			<div className='wut'>
				<span>S</span>
				<span>c</span>
				<span>r</span>
				<span>u</span>
				<span>b</span>
				<span>z</span>
				<span>#</span>
				<span>0</span>
				<span>0</span>
				<span>0</span>
				<span>1</span>
			</div>
			<section className='colorPicker'>
				<ColourWheel
					radius={85}
					padding={1}
					lineWidth={25}
					onColourSelected={rgb => changeColor(rgb)}
					onRef={ref => null}
					spacers={{
						colour: 'rgb(0, 0, 172)',
						shadowColour: 'purple',
						shadowBlur: 5,
					}}
					preset
					presetColour={'#fff'}
					animated
				/>
			</section>
			<section className='values'>
				<VoiceIcon
					className='voice'
					style={{ fill: getVoiceColor() }}
					onClick={() => setCurrentElement('voice')}
				/>
				<div
					className='voiceStatus'
					style={{ backgroundColor: elementActive('voice') }}
				></div>
				<div className='voiceDefault'>
					Voice Default
					<Radio
						checked={colorVoiceDefault()}
						onChange={() => setCurrentElement('voice')}
					/>
				</div>
				<div className='voiceActive'>
					Voice Active
					<Radio
						checked={colorVoiceActive()}
						onChange={() => setCurrentElement('voiceActive')}
					/>
				</div>
				<HealthIcon
					className='health'
					style={{ fill: values.healthColor }}
					onClick={() => setCurrentElement('health')}
				/>
				<div
					className='healthStatus'
					style={{ backgroundColor: elementActive('health') }}
				></div>
				<ArmorIcon
					className='armor'
					style={{ fill: values.armorColor }}
					onClick={() => setCurrentElement('armor')}
				/>
				<div
					className='armorStatus'
					style={{ backgroundColor: elementActive('armor') }}
				></div>
				<HungerIcon
					className='hunger'
					style={{ fill: values.hungerColor }}
					onClick={() => setCurrentElement('hunger')}
				/>
				<div
					className='hungerStatus'
					style={{ backgroundColor: elementActive('hunger') }}
				></div>
				<ThirstIcon
					className='thirst'
					style={{ fill: values.thirstColor }}
					onClick={() => setCurrentElement('thirst')}
				/>
				<div
					className='thirstStatus'
					style={{ backgroundColor: elementActive('thirst') }}
				></div>
				<StressIcon
					className='stress'
					style={{ fill: values.stressColor }}
					onClick={() => setCurrentElement('stress')}
				/>
				<div
					className='stressStatus'
					style={{ backgroundColor: elementActive('stress') }}
				></div>
			</section>
		</div>
	);
}
