///////////////////////////
///// State Values /////
///////////////////////////
const initialState = {
	menuActive: false,
	activeElement: '',
};

///////////////////////////
///// Management /////
///////////////////////////
const Menu = (state = initialState, data) => {
	switch (data.type) {
		case 'showMenu':
			return {
				...state,
				menuActive: true,
			};
		case 'setVoice':
			return {
				...state,
				voiceActiveSelector: data.value,
			};
		case 'setElement':
			return {
				...state,
				activeElement: data.value,
			};
		case 'exit':
			return {
				...state,
				menuActive: false,
				activeElement: '',
			};
		default:
			return state;
	}
};

export default Menu;
