///////////////////////////
///// Imports /////
///////////////////////////
import { useSelector } from 'react-redux';
import { store } from '../../index';
import clsx from 'clsx';
import { Grid } from '@material-ui/core';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import { ReactComponent as VoiceIcon } from '../../assets/voice.svg';
import { ReactComponent as HealthIcon } from '../../assets/health.svg';
import { ReactComponent as ArmorIcon } from '../../assets/armor.svg';
import { ReactComponent as HungerIcon } from '../../assets/hunger.svg';
import { ReactComponent as ThirstIcon } from '../../assets/thirst.svg';
import { ReactComponent as StressIcon } from '../../assets/stress.svg';
import 'react-circular-progressbar/dist/styles.css';
import './Main.scss';

///////////////////////////
///// Default /////
///////////////////////////
export default function Hud() {
	const values = useSelector(state => state.Hud);
	const voiceLevel = () => {
		if (values.voiceLevel === 1) {
			return 33;
		} else if (values.voiceLevel === 2) {
			return 66;
		} else {
			return 100;
		}
	};
	const voiceActive = () => {
		if (values.voiceActive) {
			return values.voiceActiveColor;
		}
		return values.voiceColor;
	};
	return (
		<div
			className={clsx('hud', {
				'hud-Show': values.isShowing,
			})}
		>
			<Grid
				container
				direction='row'
				justify='space-evenly'
				alignItems='center'
			>
				<section className='container'>
					<CircularProgressbar
						value={voiceLevel()}
						strokeWidth={50}
						styles={buildStyles({
							strokeLinecap: 'butt',
							pathColor: voiceActive(),
							trailColor: 'grey',
						})}
					/>
					<div className='voiceCover'>
						<VoiceIcon className='voiceIcon' style={{ fill: 'white' }} />
					</div>
				</section>
				<section className='container'>
					<CircularProgressbar
						value={values.health}
						strokeWidth={50}
						styles={buildStyles({
							strokeLinecap: 'butt',
							pathColor: values.healthColor,
							trailColor: 'grey',
						})}
					/>
					<div className='healthCover'>
						<HealthIcon className='healthIcon' style={{ fill: 'white' }} />
					</div>
				</section>
				<section className='container'>
					<CircularProgressbar
						value={values.armor}
						strokeWidth={50}
						styles={buildStyles({
							strokeLinecap: 'butt',
							pathColor: values.armorColor,
							trailColor: 'grey',
						})}
					/>
					<div className='armorCover'>
						<ArmorIcon className='armorIcon' style={{ fill: 'white' }} />
					</div>
				</section>
				<section className='container'>
					<CircularProgressbar
						value={values.hunger}
						strokeWidth={50}
						styles={buildStyles({
							strokeLinecap: 'butt',
							pathColor: values.hungerColor,
							trailColor: 'grey',
						})}
					/>
					<div className='hungerCover'>
						<HungerIcon className='hungerIcon' style={{ fill: 'white' }} />
					</div>
				</section>
				<section className='container'>
					<CircularProgressbar
						value={values.thirst}
						strokeWidth={50}
						styles={buildStyles({
							strokeLinecap: 'butt',
							pathColor: values.thirstColor,
							trailColor: 'grey',
						})}
					/>
					<div className='thirstCover'>
						<ThirstIcon className='thirstIcon' style={{ fill: 'white' }} />
					</div>
				</section>
				<section className='container'>
					<CircularProgressbar
						value={values.stress}
						strokeWidth={50}
						styles={buildStyles({
							strokeLinecap: 'butt',
							pathColor: values.stressColor,
							trailColor: 'grey',
						})}
					/>
					<div className='stressCover'>
						<StressIcon className='stressIcon' style={{ fill: 'white' }} />
					</div>
				</section>
			</Grid>
		</div>
	);
}
